const uiDiv = document.getElementById('ui')!;
const canvas = document.createElement('canvas');
uiDiv.appendChild(canvas);
const ctx = canvas.getContext('2d')!;
canvas.tabIndex = 0;
canvas.focus();

// ----- CodeMirror setup -----
const codemirrorContainer = document.getElementById('codemirror-container')!;
const codemirrorToggle = document.getElementById('codemirror-toggle')!;
let codemirrorEditor: EditorView | null = null;
let isCodemirrorVisible = true;

const STORAGE_KEY = 'saved-program';

// Load content from localStorage
const savedContent = localStorage.getItem(STORAGE_KEY) || ``;

// Save function
function saveEditorContents(view: EditorView) {
  const content = view.state.doc.toString();
  localStorage.setItem(STORAGE_KEY, content);
  reloadProgram();
  return true; // Indicates the key binding was handled
}

// Run selected code or current line
function runSelectedCode(view: EditorView) {
  let selection = view.state.selection.main;

  if (selection.empty) {
    const line = view.state.doc.lineAt(selection.head);
    view.dispatch({
      selection: { anchor: line.from, head: line.to },
    });
    selection = view.state.selection.main;
  }

  const code = view.state.doc.sliceString(selection.from, selection.to);
  runCode(code);

  return true; // Indicates the key binding was handled
}

// JavaScript linter for parse errors
const jsLinter = linter((view) => {
  const diagnostics: any[] = [];
  const tree = javascriptLanguage.parser.parse(view.state.doc.toString());

  // Check for parse errors
  tree.iterate({
    enter(node) {
      if (node.type.isError) {
        const from = node.from;
        const to = node.to;
        diagnostics.push({
          from,
          to,
          severity: 'error',
          message: `syntax error`,
        });
      }
    },
  });

  // Also try to catch runtime parse errors with more detailed messages
  const code = view.state.doc.toString();
  try {
    // This will catch some errors that the parser might miss
    new Function(code);
  } catch (error: any) {
    // Only add if we don't already have an error at this location
    const errorMessage = error.message || 'Parse error';
    if (!diagnostics.some((d) => d.message.includes(errorMessage))) {
      // Try to extract position from error
      const match = errorMessage.match(/(\d+):(\d+)/);
      if (match) {
        const lineNum = parseInt(match[1], 10);
        const colNum = parseInt(match[2], 10);
        if (lineNum <= view.state.doc.lines) {
          const line = view.state.doc.line(lineNum);
          const pos = line.from + Math.min(colNum - 1, line.length);
          diagnostics.push({
            from: pos,
            to: Math.min(pos + 10, line.to),
            severity: 'error',
            message: errorMessage,
          });
        }
      } else {
        // Fallback: add at first problematic area
        if (diagnostics.length === 0) {
          diagnostics.push({
            from: 0,
            to: Math.min(50, code.length),
            severity: 'error',
            message: errorMessage,
          });
        }
      }
    }
  }

  return diagnostics;
});

// Initialize CodeMirror
const startState = EditorState.create({
  doc: savedContent,
  extensions: [
    javascript(),
    indentOnInput(),
    bracketMatching(),
    search({ caseSensitive: false, regexp: false }),
    lintGutter(),
    jsLinter,
    keymap.of([
      {
        key: 'Mod-s',
        preventDefault: true,
        run: saveEditorContents,
      },
      {
        key: 'Mod-d',
        preventDefault: true,
        run: runSelectedCode,
      },
      ...defaultKeymap,
      ...searchKeymap,
    ]),
    EditorView.theme({
      '&': {
        height: '100%',
      },
      '.cm-scroller': {
        overflow: 'auto',
        height: '100%',
      },
    }),
  ],
});
codemirrorEditor = new EditorView({
  state: startState,
  parent: codemirrorContainer,
});

// Toggle CodeMirror visibility
codemirrorToggle.addEventListener('click', () => {
  isCodemirrorVisible = !isCodemirrorVisible;
  if (isCodemirrorVisible) {
    codemirrorContainer.classList.remove('collapsed');
    uiDiv.classList.add('codemirror-visible');
  } else {
    codemirrorContainer.classList.add('collapsed');
    uiDiv.classList.remove('codemirror-visible');
  }
  resize();
  // Notify CodeMirror to update its layout after toggle
  if (codemirrorEditor) {
    setTimeout(() => {
      codemirrorEditor?.requestMeasure();
    }, 300); // Wait for CSS transition to complete
  }
});

// ----- resizing -----

const screen = { width: innerWidth, height: innerHeight };

function resize() {
  // Calculate available width based on CodeMirror visibility
  const availableWidth = isCodemirrorVisible ? (innerWidth * 2) / 3 : innerWidth;

  // set the canvas size
  canvas.style.width = availableWidth + 'px';
  canvas.style.height = innerHeight + 'px';

  // calculate the internal pixel dimensions of the canvas
  canvas.width = availableWidth * devicePixelRatio;
  canvas.height = innerHeight * devicePixelRatio;

  ctx.scale(devicePixelRatio, devicePixelRatio);

  screen.width = innerWidth;
  screen.height = innerHeight;
}

window.addEventListener('resize', () => {
  resize();
  // Notify CodeMirror to update its layout
  if (codemirrorEditor) {
    codemirrorEditor.requestMeasure();
  }
});
resize();

// ----- rendering -----

let status = '';
let statusTime = 0;

function show(msg: any) {
  status = '' + msg;
  statusTime = Date.now();
}

let lastTimestamp = 0;

let prevX = -1;
let prevY = -1;
let prevAngle = 0;
const pointerPos: Position = { x: -1, y: -1 };
let pickedUpSprite: Sprite | null = null;

function render(timestamp: number) {
  ctx.clearRect(0, 0, innerWidth, innerHeight);
  Sprite.all.forEach((s) => {
    // ctx.save();
    // ctx.strokeStyle = 'blue';
    // const bb = s.boundingBox();
    // ctx.strokeRect(bb.x, bb.y, bb.w, bb.h);
    // ctx.restore();

    s.drawOn(ctx, pickedUpSprite === s);
  });

  if (status.length > 0 && Date.now() - statusTime < 5_000) {
    ctx.save();
    ctx.font = '20pt sans-serif';
    ctx.beginPath();
    ctx.fillStyle = '#00ff';
    ctx.fillText(status, 20, innerHeight - 20);
    ctx.fill();
    ctx.restore();
  }

  const dt = timestamp - lastTimestamp;
  Sprite.timePassed(dt);
  lastTimestamp = timestamp;

  requestAnimationFrame(render);
}

(window as any).show = show;

render(0);

// ----- add a couple objects -----

document.addEventListener('DOMContentLoaded', () => {
  reloadProgram();
});

function reloadProgram() {
  Sprite.clearAll();
  loadSampleCostumes();
  const code = codemirrorEditor!.state.doc.toString();
  resetGlobals();
  runCode(code);
}

let globals: { [name: string]: any } = {};

function resetGlobals() {
  function sprite(costumeName: string, x?: number, y?: number, scale?: number) {
    return new Sprite(costumeName, x, y, scale);
  }

  function setBackgroundColor(colorName: string) {
    const color = Color[colorName];
    document.body.style.backgroundColor = `rgb(${color.r}, ${color.g}, ${color.b})`;
  }

  globals = {
    screen, // for width and height
    Sprite,
    sprite,
    show,
    setBackgroundColor,
    console,
    abs: Math.abs,
    floor: Math.floor,
    ceil: Math.ceil,
    round: Math.round,
    min: Math.min,
    max: Math.max,
    pow: Math.pow,
    sqrt: Math.sqrt,
    sin: Math.sin,
    cos: Math.cos,
    tan: Math.tan,
    atan2: Math.atan2,
    random: Math.random,
  };

  (window as any).___globals___ = new Proxy(globals, {
    // The 'has' trap is critical; it tells the 'with' block
    // to treat every variable name as a property of this object.
    has(target, key) {
      return true;
    },

    // The 'set' trap intercepts all unqualified assignments
    // (e.g., x = 10) and 'var' declarations.
    set(target, key, value) {
      target[key as any] = value;
      return true; // Must return true for success
    },

    // The 'get' trap allows the code to read from the context.
    get(target, key) {
      if (key === Symbol.unscopables) {
        return undefined;
      }
      return target[key as any];
    },
  });
}

function runCode(code: string) {
  try {
    // (0, eval) is an 'indirect eval' call.
    // It runs in the global scope (non-strict) and sees the 'sandbox' variable.
    const ans = (0, eval)(
      `
        with (___globals___) {
          ${code}
        }
      `,
    );
    if (ans !== undefined) {
      show(` ${ans}`);
    }
  } catch (e: any) {
    show(`Error: ${e.message}`);
    console.error(e);
  }
}

function getKey(e: KeyboardEvent): string {
  return e.key === ' ' ? 'space' : e.key.toLowerCase();
}

canvas.addEventListener('keydown', (e) => {
  e.preventDefault();

  if (e.repeat) {
    return;
  }

  const key = getKey(e);
  console.log('key down', key);

  switch (key) {
    case 'shift':
      pickedUpSprite?.onDrop();
      pickedUpSprite = Sprite.at(pointerPos);
      console.log('picked up', pickedUpSprite);
      if (pickedUpSprite) {
        prevAngle = Math.atan2(pointerPos.y - pickedUpSprite.y, pointerPos.x - pickedUpSprite.x);
        pickedUpSprite.bringToFront();
        pickedUpSprite.onPickUp();
      }
      break;
    default:
      for (const sprite of Sprite.all) {
        sprite.onKeyDown(key);
      }
      break;
  }
});

canvas.addEventListener('keyup', (e) => {
  e.preventDefault();

  if (e.repeat) {
    return;
  }

  const key = getKey(e);
  console.log('key up', key);

  switch (key) {
    case 'shift':
      pickedUpSprite?.onDrop();
      pickedUpSprite = null;
      break;
    default:
      for (const sprite of Sprite.all) {
        sprite.onKeyUp(key);
      }
      break;
  }
});

canvas.addEventListener('pointerdown', (e) => {
  Sprite.at(e)?.onTap();
});

canvas.addEventListener('pointermove', (e) => {
  if (prevX < 0 || prevY < 0) {
    prevX = e.x;
    prevY = e.y;
  }

  pointerPos.x = e.x;
  pointerPos.y = e.y;

  if (pickedUpSprite) {
    if (e.ctrlKey) {
      const newAngle = Math.atan2(e.y - pickedUpSprite.y, e.x - pickedUpSprite.x);
      const diff = ((((newAngle - prevAngle + Math.PI) % TAU) + TAU) % TAU) - Math.PI;
      if (e.altKey) {
        pickedUpSprite.headingOffset -= diff;
      } else {
        pickedUpSprite.turnBy((diff * 180) / Math.PI);
      }
      prevAngle = newAngle;
    } else if (e.altKey) {
      pickedUpSprite.scale = Math.max(0.05, pickedUpSprite.scale + (e.x - prevX) / 100);
    } else {
      prevAngle = Math.atan2(e.y - pickedUpSprite.y, e.x - pickedUpSprite.x);
      pickedUpSprite.x += e.x - prevX;
      pickedUpSprite.y += e.y - prevY;
    }
  }
  prevX = e.x;
  prevY = e.y;
});
