import { Position, Rect, ColorRgb, Costume } from './types';
import { MAX_SCREEN_WIDTH, MAX_SCREEN_HEIGHT, TAU, Color } from './constants';
import * as costumes from './costumes';
import { Script } from './script';

const [collisionCanvas, collisionCtx] = createOffScreenCanvas(MAX_SCREEN_WIDTH, MAX_SCREEN_HEIGHT);
const [, mask1Ctx] = createOffScreenCanvas(MAX_SCREEN_WIDTH, MAX_SCREEN_HEIGHT);
const [, mask2Ctx] = createOffScreenCanvas(MAX_SCREEN_WIDTH, MAX_SCREEN_HEIGHT);

// TODO: make y=0 be at the bottom of the screen

export class Sprite {
  static nextId = 1;
  static readonly all: Sprite[] = [];

  static clearAll() {
    this.nextId = 1;
    this.all.length = 0;
  }

  static get(name: string): Sprite | null {
    for (const sprite of this.all) {
      if (sprite.name === name) {
        return sprite;
      }
    }
    return null;
  }

  static at(pos: Position): Sprite | null {
    for (let idx = this.all.length - 1; idx >= 0; idx--) {
      const sprite = this.all[idx];
      if (sprite.containsPoint(pos)) {
        return sprite;
      }
    }
    return null;
  }

  static allAt(pos: Position): Sprite[] {
    const sprites: Sprite[] = [];
    for (let idx = this.all.length - 1; idx >= 0; idx--) {
      const sprite = this.all[idx];
      if (sprite.containsPoint(pos)) {
        sprites.push(sprite);
      }
    }
    return sprites;
  }

  static timePassed(dt: number) {
    for (const sprite of this.all) {
      sprite.timePassed(dt);
    }
  }

  readonly id: number;
  name: string;

  // Looks
  private costume: Costume;
  isVisible = true;

  // Transform
  private pos = { x: 0, y: 0 };
  private headingInRadians = 0;
  headingOffset = Math.PI / 2; // so that heading = 0 means up
  scale = 1;

  // Variables and scripts
  private vars: { [name: string]: any } = {};

  constructor(
    costume: Costume | string = costumes.getRandom(),
    x = Math.random() * innerWidth,
    y = Math.random() * innerHeight,
    scale = 1,
  ) {
    this.id = Sprite.nextId++;
    this.name = `sprite${this.id}`;
    if (typeof costume === 'string') {
      this.costume = costumes.get(costume);
    } else {
      this.costume = costume;
    }
    this.x = x;
    this.y = y;
    this.scale = scale;
    Sprite.all.push(this);
  }

  toString() {
    return this.name;
  }

  clone(callback: (clone?: Sprite) => void) {
    const clone = new Sprite(this.costume, this.x, this.y, this.scale);
    clone.putInFrontOf(this);
    clone.isVisible = this.isVisible;
    clone.pos.x = this.pos.x + 20;
    clone.pos.y = this.pos.y + 20;
    clone.headingInRadians = this.headingInRadians;
    clone.headingOffset = this.headingOffset;
    clone.scale = this.scale;
    for (const [name, value] of Object.entries(this.vars)) {
      clone._addVar(name, value instanceof Script ? value.clone(clone) : value);
    }
    if (callback) {
      callback(clone);
    }
    return clone;
  }

  delete() {
    Sprite.all.splice(Sprite.all.indexOf(this), 1);
  }

  changeCostume(costumeName: string) {
    this.costume = costumes.get(costumeName);
  }

  moveTo(p: Position) {
    this.pos.x = p.x;
    this.pos.y = p.y;
  }

  forwardBy(n: number) {
    const angle = this.headingInRadians - this.headingOffset;
    this.pos.x += n * Math.cos(angle);
    this.pos.y += n * Math.sin(angle);
  }

  turnBy(angle: number) {
    this.headingInRadians += (angle * Math.PI) / 180;
  }

  get heading() {
    return (this.headingInRadians * 180) / Math.PI;
  }

  set heading(value: number) {
    this.headingInRadians = (value * Math.PI) / 180;
  }

  get x() {
    return this.pos.x;
  }

  set x(newValue: number) {
    this.pos.x = newValue;
  }

  get y() {
    return this.pos.y;
  }

  set y(newValue: number) {
    this.pos.y = newValue;
  }

  get leftX() {
    return this.boundingBox().x;
  }

  set leftX(newValue: number) {
    this.pos.x = newValue + this.boundingBox().w / 2;
  }

  get rightX() {
    const { x, w } = this.boundingBox();
    return x + w;
  }

  set rightX(newValue: number) {
    this.pos.x = newValue - this.boundingBox().w / 2;
  }

  get width() {
    return this.boundingBox().w;
  }

  get topY() {
    return this.boundingBox().y;
  }

  set topY(newValue: number) {
    this.pos.y = newValue + this.boundingBox().h / 2;
  }

  get bottomY() {
    const { y, h } = this.boundingBox();
    return y + h;
  }

  set bottomY(newValue: number) {
    this.pos.y = newValue - this.boundingBox().h / 2;
  }

  get height() {
    return this.boundingBox().h;
  }

  show() {
    this.isVisible = true;
  }

  hide() {
    this.isVisible = false;
  }

  // ----- variables -----

  hasVar(name: string) {
    return name in this.vars && !(this.vars[name] instanceof Script);
  }

  addVar(name: string, initialValue: any = 0) {
    if (this.hasScript(name)) {
      (window as any).show(
        `cannot replace ${this.name}'s script '${name}' with a variable!\ncall ${this.name}.removeScript('${name}') first`,
      );
    } else if (this.hasVar(name)) {
      this.vars[name] = initialValue;
    } else {
      this._addVar(name, initialValue);
    }
  }

  removeVar(name: string) {
    if (!(this.vars[name] instanceof Script)) {
      this._removeVar(name);
    }
  }

  // ----- scripts -----

  hasScript(name: string) {
    return name in this.vars && this.vars[name] instanceof Script;
  }

  script(name: string, code: () => void): Script;
  script(code: () => void): Script;
  script(nameOrCode: string | (() => void), optCode?: () => void): Script {
    return this._addScript(nameOrCode, optCode);
  }

  addScript(name: string, code: () => void): Script;
  addScript(code: () => void): Script;
  addScript(nameOrCode: string | (() => void), optCode?: () => void): Script {
    return this._addScript(nameOrCode, optCode);
  }

  private _addScript(nameOrCode: string | (() => void), optCode?: () => void): Script {
    const name =
      typeof nameOrCode === 'string' ? nameOrCode : `script${Math.round(Math.random() * 100_000)}`;
    const code = typeof nameOrCode === 'function' ? nameOrCode : optCode!;
    const script = new Script(this, code);
    if (this.hasVar(name)) {
      (window as any).show(
        `cannot replace ${this.name}'s variable '${name}' with a script! call ${this.name}.removeVar('${name}') first`,
      );
    } else if (this.hasScript(name)) {
      this.vars[name] = script;
    } else {
      this._addVar(name, script);
    }
    return script;
  }

  removeScript(name: string) {
    if (this.vars[name] instanceof Script) {
      return this._removeVar(name);
    }
  }

  timePassed(dt: number) {
    this.scriptsDo((s) => s.timePassed(dt));
  }

  onTap() {
    this.scriptsDo((s) => {
      if (s.trigger?.type === 'tap') {
        s.run();
      }
    });
  }

  onKeyDown(key: string) {
    this.scriptsDo((s) => {
      if (s.trigger?.type === 'key down' && s.trigger.key.toLowerCase() === key) {
        s.run();
      }
    });
  }

  onKeyUp(key: string) {
    this.scriptsDo((s) => {
      if (s.trigger?.type === 'key up' && s.trigger.key.toLowerCase() === key) {
        s.run();
      }
    });
  }

  onPickUp() {
    this.scriptsDo((s) => {
      if (s.trigger?.type === 'pick up') {
        s.run();
      }
    });
  }

  onDrop() {
    this.scriptsDo((s) => {
      if (s.trigger?.type === 'drop') {
        s.run();
      }
    });
  }

  private scriptsDo(fn: (script: Script) => void) {
    for (const v of Object.values(this.vars)) {
      if (v instanceof Script) {
        fn(v);
      }
    }
  }

  // ----- z ordering -----

  bringToFront() {
    this.remove();
    Sprite.all.push(this);
  }

  sendToBack() {
    this.remove();
    Sprite.all.unshift(this);
  }

  putInFrontOf(that: Sprite) {
    const idx = Sprite.all.indexOf(that);
    if (idx >= 0) {
      this.remove();
      Sprite.all.splice(idx + 1, 0, this);
    }
  }

  putBehind(that: Sprite) {
    const idx = Sprite.all.indexOf(that);
    if (idx >= 0) {
      this.remove();
      Sprite.all.splice(idx, 0, this);
    }
  }

  remove() {
    Sprite.all.splice(Sprite.all.indexOf(this), 1);
  }

  // ----- drawing -----

  drawOn(ctx: CanvasRenderingContext2D, drawHeading = false, evenIfInsible = false) {
    if (!this.isVisible && !evenIfInsible) {
      return;
    }

    ctx.save();
    ctx.translate(this.pos.x, this.pos.y);
    ctx.rotate(this.headingInRadians);
    ctx.scale(this.scale, this.scale);
    ctx.drawImage(this.costume.canvas, -this.costume.bounds.w / 2, -this.costume.bounds.h / 2);
    ctx.restore();
    if (drawHeading) {
      const size = 15;
      const diag = Math.sqrt(this.costume.bounds.w ** 2 + this.costume.bounds.h ** 2) / 2;

      ctx.beginPath();
      ctx.fillStyle = 'blue';
      ctx.arc(this.pos.x, this.pos.y, size / 2, 0, TAU);
      ctx.fill();

      ctx.lineCap = 'round';
      ctx.strokeStyle = 'blue';
      ctx.lineWidth = 4;

      const angle = this.headingInRadians - this.headingOffset;
      const topX = this.pos.x + this.scale * diag * Math.cos(angle);
      const topY = this.pos.y + this.scale * diag * Math.sin(angle);

      ctx.beginPath();
      ctx.moveTo(this.pos.x, this.pos.y);
      ctx.lineTo(topX, topY);
      ctx.stroke();

      const angleInc = (3 * Math.PI) / 4;

      ctx.beginPath();
      ctx.moveTo(topX, topY);
      ctx.lineTo(
        topX + size * Math.cos(angle + angleInc),
        topY + size * Math.sin(angle + angleInc),
      );
      ctx.stroke();

      ctx.beginPath();
      ctx.moveTo(topX, topY);
      ctx.lineTo(
        topX + size * Math.cos(angle - angleInc),
        topY + size * Math.sin(angle - angleInc),
      );
      ctx.stroke();
    }
  }

  // ----- sensing -----

  containsPoint(worldPos: Position): boolean {
    collisionCtx.clearRect(0, 0, collisionCanvas.width, collisionCanvas.height);
    this.drawOn(collisionCtx);
    const a = collisionCtx.getImageData(worldPos.x, worldPos.y, 1, 1).data[3];
    return a > 0;
  }

  colorAt(worldPos: Position): ColorRgb {
    collisionCtx.clearRect(0, 0, collisionCanvas.width, collisionCanvas.height);
    this.drawOn(collisionCtx);
    const [r, g, b] = collisionCtx.getImageData(worldPos.x, worldPos.y, 1, 1).data;
    return { r, g, b };
  }

  colorSees(thisColor: string, that: Sprite, tolerance = 20) {
    return this.colorSeesColor(thisColor, 'white', tolerance, 255, that);
  }

  colorSeesColor(
    _thisColor: string,
    _thatColor: string,
    tolerance1 = 20,
    tolerance2 = tolerance1,
    _that: Sprite | null = null,
  ): boolean {
    const thisColor = Color[_thisColor];
    if (!thisColor) {
      (window as any).show('unknown color: ' + _thisColor);
      return false;
    }

    const thatColor = Color[_thatColor];
    if (!thatColor) {
      (window as any).show('unknown color: ' + _thatColor);
      return false;
    }

    const bb = this.fastBoundingBox();
    const x = Math.max(0, bb.x);
    const y = Math.max(0, bb.y);
    const w = Math.min(collisionCanvas.width, bb.w);
    const h = Math.min(collisionCanvas.height, bb.h);
    if (w <= 0 || h <= 0) {
      return false;
    }

    mask1Ctx.clearRect(x, y, w, h);
    mask2Ctx.clearRect(x, y, w, h);
    this.drawMaskOn(mask1Ctx, thisColor, tolerance1);
    if (_that) {
      _that.drawMaskOn(mask2Ctx, thatColor, tolerance2);
    } else {
      for (const that of Sprite.all) {
        if (that !== this) {
          that.drawMaskOn(mask2Ctx, thatColor, tolerance2);
        }
      }
    }

    const aData = mask1Ctx.getImageData(x, y, w, h).data;
    const bData = mask2Ctx.getImageData(x, y, w, h).data;

    for (let idx = 0; idx < aData.length; idx += 4) {
      if (aData[idx + 3] > 0 && bData[idx + 3] > 0) {
        return true;
      }
    }
    return false;
  }

  // helpers

  private _addVar(name: string, value: any) {
    this.vars[name] = value;
    Object.defineProperty(this, name, {
      get() {
        return this.vars[name];
      },
      set(value: any) {
        this.vars[name] = value;
      },
      configurable: true,
    });
  }

  private _removeVar(name: string) {
    if (name in this.vars) {
      delete this.vars[name];
      delete (this as any)[name];
    }
  }

  private drawMaskOn(ctx: CanvasRenderingContext2D, color: ColorRgb, tolerance: number) {
    ctx.save();
    ctx.translate(this.pos.x, this.pos.y);
    ctx.rotate(this.headingInRadians);
    ctx.scale(this.scale, this.scale);
    ctx.globalCompositeOperation = 'lighter';
    ctx.imageSmoothingEnabled = false;
    ctx.drawImage(
      this.getMask(color, tolerance),
      -this.costume.bounds.w / 2,
      -this.costume.bounds.h / 2,
    );
    ctx.restore();
  }

  readonly masks = new Map<HTMLImageElement | HTMLCanvasElement, Map<string, HTMLCanvasElement>>();

  private getMask(color: ColorRgb, tolerance: number) {
    const masksByKey = this.getMasksByKey();
    const key = `${color.r}:${color.g}:${color.b}:${tolerance}`;
    let mask = masksByKey.get(key);
    if (mask) {
      return mask;
    }

    mask = document.createElement('canvas');
    mask.width = this.costume.bounds.w;
    mask.height = this.costume.bounds.h;
    masksByKey.set(key, mask);

    const ctx = mask.getContext('2d')!;
    const src = this.costume.ctx.getImageData(
      0,
      0,
      this.costume.bounds.w,
      this.costume.bounds.h,
    ).data;
    const out = ctx.createImageData(this.costume.bounds.w, this.costume.bounds.h);
    const dst = out.data;

    for (let idx = 0; idx < src.length; idx += 4) {
      const r = src[idx];
      const g = src[idx + 1];
      const b = src[idx + 2];
      const a = src[idx + 3];

      if (
        a > 0 &&
        Math.abs(r - color.r) <= tolerance &&
        Math.abs(g - color.g) <= tolerance &&
        Math.abs(b - color.b) <= tolerance
      ) {
        // White pixel (binary mask)
        dst[idx] = 255;
        dst[idx + 1] = 255;
        dst[idx + 2] = 255;
        dst[idx + 3] = 255;
      }
    }

    ctx.putImageData(out, 0, 0);
    return mask;
  }

  private getMasksByKey() {
    let maskMap = this.masks.get(this.costume.element);
    if (!maskMap) {
      maskMap = new Map<string, HTMLCanvasElement>();
      this.masks.set(this.costume.element, maskMap);
    }
    return maskMap;
  }

  boundingBox(): Rect {
    const dx = innerHeight / 2 - this.pos.x;
    const dy = innerHeight / 2 - this.pos.y;
    this.pos.x += dx;
    this.pos.y += dy;

    collisionCtx.clearRect(0, 0, collisionCanvas.width, collisionCanvas.height);
    this.drawOn(collisionCtx, false, true);

    const fbb = this.fastBoundingBox();
    const x0 = Math.max(0, fbb.x);
    const xf = x0 + fbb.w;
    const y0 = Math.max(0, fbb.y);
    const yf = y0 + fbb.h;
    const data = collisionCtx.getImageData(x0, y0, fbb.w, fbb.h).data;

    let minX = Infinity;
    let minY = Infinity;
    let maxX = -Infinity;
    let maxY = -Infinity;
    for (let y = y0; y < yf; y++) {
      for (let x = x0; x < xf; x++) {
        const idx = ((y - y0) * fbb.w + x - x0) * 4;
        const alpha = data[idx + 3];
        if (alpha > 0) {
          minX = Math.min(minX, x);
          minY = Math.min(minY, y);
          maxX = Math.max(maxX, x);
          maxY = Math.max(maxY, y);
        }
      }
    }

    this.pos.x -= dx;
    this.pos.y -= dy;
    minX -= dx;
    minY -= dy;
    maxX -= dx;
    maxY -= dy;

    return {
      x: minX,
      y: minY,
      w: maxX - minX,
      h: maxY - minY,
    };
  }

  fastBoundingBox(): Rect {
    const { x, y, w, h } = this.localBoundingBox();
    const corner1 = this.localToWorld({ x, y });
    const corner2 = this.localToWorld({ x, y: y + h });
    const corner3 = this.localToWorld({ x: x + w, y });
    const corner4 = this.localToWorld({ x: x + w, y: y + h });

    const minX = Math.min(corner1.x, corner2.x, corner3.x, corner4.x);
    const minY = Math.min(corner1.y, corner2.y, corner3.y, corner4.y);
    const maxX = Math.max(corner1.x, corner2.x, corner3.x, corner4.x);
    const maxY = Math.max(corner1.y, corner2.y, corner3.y, corner4.y);

    return {
      x: Math.floor(minX),
      y: Math.floor(minY),
      w: Math.ceil(maxX - minX),
      h: Math.ceil(maxY - minY),
    };
  }

  localBoundingBox(): Rect {
    return this.costume.bounds;
  }

  private localToWorld(p: Position): Position {
    // Apply scale
    let sx = p.x * this.scale;
    let sy = p.y * this.scale;

    // Apply rotation
    const cos = Math.cos(this.headingInRadians);
    const sin = Math.sin(this.headingInRadians);

    const rx = sx * cos - sy * sin;
    const ry = sx * sin + sy * cos;

    // Apply translation
    return {
      x: rx + this.pos.x,
      y: ry + this.pos.y,
    };
  }
}

// helpers

function createOffScreenCanvas(
  w: number,
  h: number,
): [HTMLCanvasElement, CanvasRenderingContext2D] {
  const canvas = document.createElement('canvas');
  canvas.width = w;
  canvas.height = h;
  return [canvas, canvas.getContext('2d', { willReadFrequently: true })!];
}
