import { Trigger } from './types';

export function triggerToString(t: Trigger) {
  switch (t.type) {
    case 'clock':
      return `${t.hz} Hz`;
    case 'tap':
      return 'tap';
    case 'key down':
      return `${t.key} down`;
    case 'key up':
      return `${t.key} up`;
    case 'pick up':
      return 'pick up';
    case 'drop':
      return 'drop';
  }
}

export function triggerFromString(s: String): Trigger | null {
  const parts = s.toLowerCase().split(' ');

  if (parts.length === 1) {
    switch (parts[0]) {
      case 'drop':
        return { type: 'drop' };
      case 'tap':
        return { type: 'tap' };
    }
  } else if (parts.length === 2 && parts[0] === 'pick' && parts[1] === 'up') {
    return { type: 'pick up' };
  } else if (parts.length === 2) {
    switch (parts[1]) {
      case 'hz': {
        const value = parseInt(parts[0]);
        return !Number.isNaN(value) ? { type: 'clock', hz: value } : null;
      }
      case 'down':
        return { type: 'key down', key: parts[0] };
      case 'up': {
        return { type: 'key up', key: parts[0] };
      }
    }
  }
  return null;
}
