import { triggerFromString, triggerToString } from './lib';
import { Sprite } from './sprite';
import { Trigger } from './types';

export class Script {
  public trigger: Trigger | null = null;
  private triggerPeriod = Infinity;
  private accumulatedTime = 0;
  public error: any = null;

  constructor(
    private self: Sprite,
    public code: (self: Sprite) => void,
    trigger: string = '',
  ) {
    this.setTrigger(trigger);
  }

  toString() {
    return `a script that belongs to ${this.self.name}`;
  }

  clone(newSelf: Sprite) {
    const script = new Script(newSelf, this.code);
    script.setTrigger(this.trigger ? triggerToString(this.trigger) : '');
    return script;
  }

  setTrigger(t: string) {
    this.trigger = triggerFromString(t);
    this.accumulatedTime = 0;
    this.triggerPeriod =
      this.trigger && 'hz' in this.trigger
        ? (this.triggerPeriod = 1000 / this.trigger.hz)
        : Infinity;
  }

  run(numTimes = 1) {
    this.error = null;
    try {
      for (let i = 0; i < numTimes; i++) {
        this.code(this.self);
      }
    } catch (e) {
      this.error = e;
    }
  }

  startRunning(hz = 60) {
    this.setTrigger(`${hz} hz`);
  }

  stopRunning() {
    this.setTrigger('');
  }

  get isRunning() {
    return this.trigger && 'hz' in this.trigger;
  }

  onClick() {
    this.onTap();
  }

  onTap() {
    this.setTrigger('tap');
  }

  onKeyDown(key: string) {
    this.setTrigger(`${key} down`);
  }

  onKeyUp(key: string) {
    this.setTrigger(`${key} up`);
  }

  timePassed(dt: number) {
    this.accumulatedTime += dt;
    while (this.accumulatedTime > this.triggerPeriod) {
      this.run();
      this.accumulatedTime -= this.triggerPeriod;
    }
  }
}
